"use client";

import { useState, useEffect } from "react";
import { SelicPoint } from "../types";

import { Pagination } from "@/components/ui/Pagination";
import { sortData } from "@/components/ui/DataTable/sort";
import { paginate } from "@/components/ui/DataTable/paginate";
import { SortState } from "@/components/ui/DataTable/types";

type Props = {
  data: SelicPoint[];
};

export function SelicTable({ data }: Props) {
  const [page, setPage] = useState(1);
  const [sort, setSort] = useState<SortState<SelicPoint>>({
    key: "date",
    direction: "desc",
  });

  const PAGE_SIZE = 10;

  // sempre volta pra página 1 quando muda dataset ou ordenação
  useEffect(() => {
    setPage(1);
  }, [data, sort]);

  const sortedData = sortData(data, sort);
  const { pageData, totalPages } = paginate(
    sortedData,
    page,
    PAGE_SIZE
  );

  function toggleSort(key: keyof SelicPoint) {
    setSort((prev) => {
      if (prev.key === key) {
        return {
          key,
          direction: prev.direction === "asc" ? "desc" : "asc",
        };
      }

      return { key, direction: "asc" };
    });
  }

  return (
    <div className="space-y-3">
      <table className="w-full border-collapse text-sm">
        <thead>
          <tr className="border-b text-left text-zinc-600">
            <th
              className="py-2 cursor-pointer select-none"
              onClick={() => toggleSort("date")}
            >
              Data
            </th>
            <th
              className="py-2 cursor-pointer select-none"
              onClick={() => toggleSort("value")}
            >
              Taxa SELIC (%)
            </th>
          </tr>
        </thead>

        <tbody>
          {pageData.map((item) => (
            <tr
              key={item.date.toISOString()}
              className="border-b hover:bg-zinc-50"
            >
              <td className="py-2">
                {item.date.toLocaleDateString("pt-BR")}
              </td>
              <td className="py-2 font-medium">
                {new Intl.NumberFormat("pt-BR", {
                  minimumFractionDigits: 2,
                  maximumFractionDigits: 2,
                }).format(item.value)}
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <Pagination
        page={page}
        totalPages={totalPages}
        onChange={setPage}
      />
    </div>
  );
}
